  line-height: 29px;
  text-align: center;
  color: #FFFFFF;
  width: 280px;
  padding-bottom: 50px;
}
.application__link {
  border: 3px solid #FFFFFF;
  border-radius: 29px;
  padding-left: 39px;
  padding-right: 39px;
  padding-top: 19px;
  padding-bottom: 19px;
  font-family: "Open Sans Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
}
.application__img-wrapper {
